.container {
  min-height: 705px;
  padding-bottom: 80px;
}
.childContainer {
  flex-direction: column;
  justify-content: center;
}
.memberContainer {
  display: flex;
  width: 100%;
  flex-direction: row;
  justify-content: space-evenly;
  margin-top: 40px;
}
@media only screen and (max-width: 1400px) {
  .memberContainer {
    flex-direction: column;
  }
}
